.row {
  color       : white;
  margin-left : 20px;

  .row__posters {
    display    : flex;
    overflow-y : hidden;
    overflow-x : scroll;
    padding    : 20px;

    /* For all browsers */
    &::-webkit-scrollbar {
      display : none;
    }

    .row__poster {
      width        : 100%;
      object-fit   : contain;
      max-height   : 100px;
      margin-right : 10px;
      transition   : transform 300ms;

      &:hover {
        transform : scale(1.08);
        cursor: pointer;
      }
    }

    .row__posterLarge {
      max-height : 250px;

      &:hover {
        transform : scale(1.09);
      }
    }
  }
}
